import React, { useState } from "react"
import ListOfHackers from "../ListOfHackers"
import RegistrationDropdown from "./Dropdown"
import { Input, Button, Icon, Divider } from "antd"

const db= [
  {
      "id": "50",
      "nombre": "Chris Fuentes",
      "email": "chris@gmail.com"
  },
  {
      "id": "60",
      "nombre": "Jorge Monge",
      "email": "monge@gmail.com"
  },
  {
      "id": "70",
      "nombre": "Denny Portillo",
      "email": "denny@gmail.com"
  },
  {
      "id": "80",
      "nombre": "Majo",
      "email": "majo@gmail.com"
  }
]

export default ({QR=console.info,setQRView=console.info}) => {
  const [category, setCategory] = useState({})
  const [hackers, setHackers] = useState([])

  const scanQR = () =>{
    window.cordova.plugins.barcodeScanner.scan(
      function (result) {
         const value = db.filter((data)=>{
          return data.id==result.text
         })

         if(value.length>0){
           alert('Hacker encontrado')
           const clone = [...hackers]
           clone.push(value[0])
           setHackers(clone)
         }else{
           alert('Hacker no encontrado')
         }

      },
      function (error) {
          alert("Scanning failed: " + error);
      },
      {
          preferFrontCamera : false, // iOS and Android
          showFlipCameraButton : true, // iOS and Android
          showTorchButton : true, // iOS and Android
          prompt : "Scan your code", // Android
          resultDisplayDuration: 500, // Android, display scanned text for X ms. 0 suppresses it entirely, default 1500
          orientation : "vertical", // Android only (portrait|landscape), default unset so it rotates with the device
      }
   );
  }

  const setQR = () =>{
    setQRView(!QR)
    console.log(QR)
  }

  return (
    <div className="flex flex-col">
      <Input placeholder="Team name" size="large" className="w-full mt-2" />
      <RegistrationDropdown category={category} setCategory={setCategory} />
      <ListOfHackers hackers={hackers} setHackers={setHackers}/>
      <Button onClick={scanQR} className="flex items-center justify-center">
        <Icon type="plus" /> Add hacker
      </Button>
      <Divider />
      <Button type="primary" size="large" className="mt-2 w-full">
        Register team
      </Button>
      <Button type="dashed" className="mt-2 w-full">
        Cancel registration
      </Button>
    </div>
  )
}
